import { Component, OnInit } from '@angular/core';
import {NewsService} from '../news.service'


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {


  term:string ="";



  usersContainer = [];
  newsContainer = [];
  firstNews = {};
  featuredNews:any = [];

  currentCountryCode= 'eg';
  currentCategory= 'general';

  countries:object[]=
  [
    {countryCode:'eg', countryName:'Egypt'},
    {countryCode:'us', countryName:'Amirca'},
    {countryCode:'au', countryName:'Australia'},
    {countryCode:'it', countryName:'Italy'},
    {countryCode:'de', countryName:'Germany'},

  ]


categories = ["business" , "entertainment" , "health" , "general" , "sports" , "technology"]

  constructor(public _NewsService:NewsService) {


    this._NewsService.getNews(this.currentCountryCode,this.currentCategory).subscribe(  data =>{
      this.newsContainer = data.articles

    }  )
      }
  changeCountry(code)
  {
    this.currentCountryCode = code;
    this._NewsService.getNews(this.currentCountryCode,this.currentCategory).subscribe(
      data => this.newsContainer = data.articles )

  }

  changeCategory(c) {
    this.currentCategory = c;
    this._NewsService.getNews(this.currentCountryCode,this.currentCategory).subscribe(  data => {
      this.newsContainer = data.articles;
    });

  }



  ngOnInit() {
  }

}


