import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'seemore'
})
export class SeemorePipe implements PipeTransform {

  transform(desc:string , limit:number) {


    if(desc != null)
      {
        if(limit == undefined)
          {
            limit = 3;
          }
        return desc.substring(0,limit);
        
      }

  }

}
